"use client";

import axios from "axios";
import { useParams } from "next/navigation";
import { Router } from "next/router";
import React, { useEffect, useState } from "react";

interface Kelas {
  _id: string;
  title: string;
  description: string;
  price: number;
  imageLink: string;
}

const Page = () => {
  const params = useParams();

  const [kelas, setKelas] = useState<Kelas>({
    _id: "",
    title: "",
    description: "",
    price: 0,
    imageLink: "",
  });

  const getKelas = async () => {
    try {
      const token = localStorage.getItem("token");
      if (!token) {
        console.error("Token tidak ditemukan, silakan login kembali.");
        return;
      }

      const response = await axios.get("http://localhost:8080/api/kelas", {
        headers: { Authorization: `Bearer ${token}` },
      });

      const dataKelas = response.data.payload.find(
        (data: Kelas) => data._id === params.id
      );
      if (dataKelas) {
        setKelas(dataKelas);
      } else {
        console.warn("Data kelas tidak ditemukan.");
      }
    } catch (error) {
      console.error("Kesalahan pada server:", error);
    }
  };

  const editHandle = (e: React.FormEvent) => {
    e.preventDefault();
    const token = localStorage.getItem("token");
    if (!token) {
      console.error("Token tidak ditemukan, silakan login kembali.");
      return;
    }

    axios
      .put(`http://localhost:8080/api/kelas/${kelas._id}`, kelas, {
        headers: { Authorization: `Bearer ${token}` },
      })
      .then((res) => {
        console.log(res);
        Router.push("/dashboard/kelas");
      });
  };

  useEffect(() => {
    console.log("Fetching data for ID:", params.id);
    getKelas();
  }, [params.id]);

  return (
    <div>
      <h1>Halaman Kelas {params.id}</h1>
      <form onSubmit={editHandle}>
        <label htmlFor="title">Title</label>
        <input
          type="text"
          name="title"
          id="title"
          className="border-2 border-gray-300 p-2 rounded-lg"
          value={kelas.title}
          onChange={(e) => setKelas({ ...kelas, title: e.target.value })}
        />

        <label htmlFor="description">Description</label>
        <input
          type="text"
          name="description"
          id="description"
          className="border-2 border-gray-300 p-2 rounded-lg"
          value={kelas.description}
          onChange={(e) => setKelas({ ...kelas, description: e.target.value })}
        />

        <label htmlFor="price">Price</label>
        <input
          type="number"
          name="price"
          id="price"
          className="border-2 border-gray-300 p-2 rounded-lg"
          value={kelas.price}
          onChange={(e) =>
            setKelas({ ...kelas, price: Number(e.target.value) })
          }
        />

        <label htmlFor="image">Image</label>
        <input
          type="text"
          name="image"
          id="image"
          className="border-2 border-gray-300 p-2 rounded-lg"
          value={kelas.imageLink}
          onChange={(e) => setKelas({ ...kelas, imageLink: e.target.value })}
        />
        <button type="submit">Submit</button>
      </form>
    </div>
  );
};

export default Page;
